  font-style: normal;
}

@font-face {
  font-family: 'Novecento-Bold';
  src: url('fonts/Novecento/fonts/novecentosanswide-demibold-webfont.eot');
  src: url('fonts/Novecento/fonts/novecentosanswide-demibold-webfont.eot?#iefix')
      format('embedded-opentype'),
    url('fonts/Novecento/fonts/novecentosanswide-demibold-webfont.woff2') format('woff2'),
    url('fonts/Novecento/fonts/novecentosanswide-demibold-webfont.woff') format('woff'),
    url('fonts/Novecento/fonts/novecentosanswide-demibold-webfont.ttf') format('truetype'),
