.home-banner {
  width: 100%;
  height: 500px;

  .hero-image {
    width: 100%;
    height: 500px;
    object-fit: cover;
    position: absolute;
    top: 0;
    filter: brightness(50%);
  }

  .banner-logo {
    width: 200px;
    position: absolute;
    left: 40%;
    top: 20%;
  }
}

h1 {
  text-transform: uppercase;
  font-size: 25px;
  text-align: center;
}

.shop-stuff {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 20px;
  padding: 50px;

  div {
    position: relative;
    border: 0.1px solid #a1a1a1;
    height: 300px;

    img {
      display: block;
      width: 100px;
      margin-left: auto;
      margin-right: auto;
    }
    button {
      text-transform: uppercase;
      font-size: 14px;
      color: white;
      background-color: #248a83;
      padding: 10px;
      margin: 0 auto;
      display: block;
    }

    p {
      text-align: center;
    }
  }
}

.inhabitent-journal {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 20px;
  padding: 50px;

  img {
    width: 100%;
    object-fit: cover;
    height: 300px;
  }

  span {
    text-transform: uppercase;
    color: #248a83;
  }

  button {
    text-transform: uppercase;
    font-size: 14px;
    color: black;
    background: none;
    padding-right: 5px;
    padding-left: 5px;
    position: relative;
  }
}

.latest-adventures {
  display: grid;
  grid-template-areas:
    'a a b b'
    'a a c d';
  grid-gap: 20px;
  padding: 50px;

  img {
    width: 100%;
    object-fit: cover;
  }

  .item1 {
    grid-area: a;
    height: 100%;
  }

  .item2 {
    grid-area: b;
    height: 250px;
  }

  .item3 {
    grid-area: c;
  }

  .item4 {
    grid-area: d;
  }

  button {
    text-transform: uppercase;
    font-size: 14px;
    color: white;
    background-color: #248a83;
    padding: 5px;
    position: relative;
  }
}
